import React, { Component } from 'react';
import { CarouselProvider, Slider, Slide, Dot, Image } from 'pure-react-carousel';
import 'pure-react-carousel/dist/react-carousel.es.css';
import SetaParaBaixo from "../../assets/img/icons/seta-para-baixo.png";
import LoremImagem from "../../assets/img/lorem1.png";
import '../../assets/css/carousel.css';
import '../../assets/css/outros-produtos.css';

class CabecalhoCarousel extends Component {
    render() { 
        return (
            <>
                <CarouselProvider
                naturalSlideWidth={100}
                naturalSlideHeight={125}
                isIntrinsicHeight={true}
                totalSlides={3}
                isPlaying={true}
                interval={5000}
                infinite={true}
                className="carousel"
            >   
                <div className="carousel__marca">
                    <h1 className="carousel__titulo">Lorem Ipsum</h1>
                    <p className="carousel__slogan">Lorem ipsum dolor sit amet, consectetur adipiscing elit</p>
                </div>
                <div className="carousel__seta">
                    <a href="#chamada">
                        <button className="carousel__seta-botao">
                            <img src={SetaParaBaixo} alt="Pular para próxima sessão"/>
                        </button>
                    </a>
                </div>
                <Slider >
                    <Slide index={0} className="carousel__imagem-container">
                        <Image src={LoremImagem} alt="" className="carousel__imagem"/>
                    </Slide>
                    <Slide index={1} className="carousel__imagem-container">
                        <Image src={LoremImagem} alt="" className="carousel__imagem"/>
                    </Slide>
                    <Slide index={2} className="carousel__imagem-container">
                        <Image src={LoremImagem} alt="" className="carousel__imagem"/>
                    </Slide>
                </Slider>
                <div className="carousel__indicadores" ref={this.navegacao}>
                    <Dot slide={0} className="carousel__indicador"></Dot>
                    <Dot slide={1} className="carousel__indicador"></Dot>
                    <Dot slide={2} className="carousel__indicador"></Dot>
                </div>
            </CarouselProvider>
                
            </>
        );
    }
}

export default CabecalhoCarousel;
