import React, { Component } from 'react';
import '../../assets/css/formulario.css';

class Formulario extends Component {
    render() { 
        return (
            <section>
                <form className="formulario">
                    <ol className="formulario__container">
                        <li className="formulario__nome">
                            <label>*Nome:</label>
                            <input placeholder="Informe seu nome"/>
                        </li>
                        <li className="formulario__email">
                            <label>*E-mail:</label>
                            <input type="email" placeholder="Informe seu e-mail"/>
                        </li>
                        <li className="formulario__telefone">
                            <label>*Telefone:</label>
                            <input type="number" placeholder="(__)____-____"/>
                        </li>
                        <li className="formulario__mensagem">
                            <label>*Mensagem:</label>
                            <textarea cols="30" rows="5" placeholder="Escreva aqui"></textarea>
                        </li>
                    </ol>
                    <button className="formulario__botao" type="submit">Enviar</button>
                </form>
            </section>
        );
    }
}
 
export default Formulario;