import React, { Component } from 'react';
import { CarouselProvider, Slider, Slide, ButtonBack, ButtonNext } from 'pure-react-carousel';
import 'pure-react-carousel/dist/react-carousel.es.css';
import '../../assets/css/chamada.css';
import ChamadaCartao from './ChamadaCartao';

class Chamada extends Component {
    render() { 
        return (
            <div className="chamada" id="chamada">
                <CarouselProvider
                    naturalSlideWidth={100}
                    naturalSlideHeight={125}
                    isIntrinsicHeight={true}
                    isPlaying={true}
                    visibleSlides={1}
                    totalSlides={9}
                    interval={6000}
                    infinite={true}
                    className="chamada__carousel"
                >
                    <Slider className="chamada__slider">
                        <Slide index={0} className="chamada__slide">
                            <ChamadaCartao/>
                        </Slide>
                        <Slide index={1} className="chamada__slide">
                            <ChamadaCartao/>
                        </Slide>
                        <Slide index={2} className="chamada__slide">
                            <ChamadaCartao/>
                        </Slide>
                        <Slide index={3} className="chamada__slide">
                            <ChamadaCartao/>
                        </Slide>
                        <Slide index={4} className="chamada__slide">
                            <ChamadaCartao/>
                        </Slide>
                        <Slide index={5} className="chamada__slide">
                            <ChamadaCartao/>
                        </Slide>
                        <Slide index={6} className="chamada__slide">
                            <ChamadaCartao/>
                        </Slide>
                        <Slide index={7} className="chamada__slide">
                            <ChamadaCartao/>
                        </Slide>
                        <Slide index={8} className="chamada__slide">
                            <ChamadaCartao/>
                        </Slide>
                    </Slider>
                    <div className="chamada__setas">
                        <ButtonBack disabled={false} className="botao chamada__seta-esquerda"></ButtonBack>
                        <ButtonNext disabled={false} className="botao chamada__seta-direita"></ButtonNext>
                    </div>
                </CarouselProvider>
            </div>
        );
    }
}
 
export default Chamada;