import React, { Component } from 'react';
import CabecalhoTitulo from './CabecalhoTitulo';
import CabecalhoCarousel from './CabecalhoCarousel';

class Cabecalho extends Component {
    render() { 
        return (
            <header>
                <CabecalhoTitulo />
                <CabecalhoCarousel />
            </header>
        );
    }
}
 
export default Cabecalho;